<template>
  <div>
    <Titletop title="个人中心">
      <router-link slot="l" replace to="/mineself"></router-link>
      <router-link slot="r" replace to="/index"></router-link>
    </Titletop>
    <PersonalSettings>
      <div class="headset">
        <div class="headset-a">
          <van-uploader
            v-model="fileList"
            :after-read="afterRead"
            multiple
            :max-size="2000 * 1024"
            @oversize="onOversize"
            :max-count="1"
          />
          <img src="https://m.jyacg.com/static/img/userheader.f76c487.png" />
        </div>
        <h4>请选择照片上传</h4>
        <p>大小为180*180像素，支持JPG、PNG等格式，图片需小于2M</p>
        <button @click="submit">提交</button>
      </div>
    </PersonalSettings>
  </div>
</template>

<script>
import Titletop from "@/components/Titletop.vue";
import PersonalSettings from "@/components/PersonalSettings.vue";
export default {
  name: "Mymyheadse",
  props: ["gets"],
  components: { Titletop, PersonalSettings },
  data() {
    return {
      fileList: [],
      img: "",
    };
  },
  mounted() {
    this.gets();
  },
  methods: {
    submit() {
      if (!this.fileList.length) {
        this.$toast("头像不能为空");
        return;
      }
      console.log(this.img)
      
      this.axios
        .post(
          "https://jyapi.jyacg.com/admin/user/personupbase",
          {
            table: "2",
            avatar: this.img,
          },
          {
            headers: {
              authorization: localStorage.getItem("api_token"),
            },
          }
        )
        .then((res) => {
          console.log(res);
          if (res.status == 200) {
            if (res.data.code == 0) {
                console.log(111);
              this.$router.replace({
                name: "mineself",
              });
              this.gets();
            }
          }
        });
    },
    onOversize(file) {
      this.$toast("文件大小不能超过 2M");
    },
    afterRead(files) {
        console.log(111);
      this.axios
        .post(
          "https://jyapi.jyacg.com/api/upload/image",
          {
            file: files.content.split(",")[1],
            type: "avatar",
          },
          {
            headers: {
              authorization: localStorage.getItem("api_token"),
            },
          }
        )
        .then((res) => {
            console.log(res);
          if (res.status == 200) {
            console.log(111);
            this.img = res.data.data;
          }
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.headset {
  width: 285px;
  height: 204px;
  padding: 20px;
  text-align: center;
  button {
    width: 118px;
    height: 30px;
    background-color: #439fff;
    color: #fff;
    border: none;
    border-radius: 15px;
  }
  h4 {
    font-size: 13px;
    color: #333;
    font-weight: 600;
    margin: 30px 0 10px;
  }
  p {
    margin: 20px;
  }
  .headset-a {
    justify-content: space-between;
    width: 205px;
    height: 66px;
    display: flex;
    border: none;
    margin: 0 40px;
    img {
      height: 80px;
      width: 80px;
    }
  }
}
</style>